var GuiGame = /** @class */ (function () {
    function GuiGame() {
        var _this = this;
        this._callback_retry = null;
        this._callback_choose_new_words = null;
        this.is_started = false;
        this.element_source_hint = document.getElementById('source-hint1');
        this.element_source_lang = document.getElementById('source-lang');
        this.element_guess_input = document.getElementById('guess-input');
        this.element_guess_input.addEventListener('keypress', function (e) { return _this.handle_key_press(e); });
        document.getElementById('game-summary-btn-retry').onclick = function (e) { return _this.handle_btn_retry(e); };
        document.getElementById('game-summary-btn-start').onclick = function (e) { return _this.handle_btn_goto_start(e); };
    }
    GuiGame.prototype.hide = function () {
        document.getElementById('game-summary').style.display = 'none';
        document.getElementById('game-board').style.display = 'none';
    };
    GuiGame.prototype.handle_btn_goto_start = function (e) {
        if (this._callback_choose_new_words != null)
            this._callback_retry();
    };
    GuiGame.prototype.handle_btn_retry = function (e) {
        if (this._callback_retry != null)
            this._callback_retry();
    };
    GuiGame.prototype.handle_key_press = function (e) {
        if (this.is_started != true)
            return;
        if (e.key != 'Enter')
            return;
        var word = this.element_guess_input.value;
        if (word == null || word == '')
            return;
        this.element_guess_input.value = '';
        this.callback_guess(word);
    };
    GuiGame.prototype.newgame = function (retry, choose_new_words) {
        this._callback_retry = retry;
        this._callback_choose_new_words = choose_new_words;
        document.getElementById('game-summary').style.display = 'none';
        this.is_started = true;
        document.getElementById('game-board').style.display = 'block';
    };
    GuiGame.prototype.show_summary = function (summary) {
        this.is_started = false;
        document.getElementById('game-board').style.display = 'none';
        document.getElementById('game-summary-success').innerText = summary.correct_words.length.toString();
        document.getElementById('game-summary-wrong').innerText = summary.wrong_words.length.toString();
        var word_list = document.getElementById('game-summary-word-list');
        while (word_list.firstChild) {
            word_list.removeChild(word_list.lastChild);
        }
        for (var i = 0; i < summary.wrong_words.length; i++) {
            word_list.appendChild(this.create_word_summary_element(summary.wrong_words[i]));
        }
        for (var i = 0; i < summary.correct_words.length; i++) {
            word_list.appendChild(this.create_word_summary_element(summary.correct_words[i]));
        }
        document.getElementById('game-summary').style.display = 'block';
    };
    GuiGame.prototype.create_word_summary_element = function (word) {
        var outer = document.createElement('div');
        outer.className = 'level summary-' + (word.success ? 'correct' : 'wrong');
        var center = document.createElement('div');
        center.className = 'level-item has-text-centered';
        var card = this._build_word_card(word.word.source, word.word.source_hint1);
        center.appendChild(card);
        var expected = this._build_word_card(word.word.target, 'Expected');
        center.appendChild(card);
        outer.appendChild(center);
        center = document.createElement('div');
        center.className = 'level-item has-text-centered';
        center.appendChild(expected);
        outer.appendChild(center);
        center = document.createElement('div');
        center.className = 'level-item has-text-centered';
        var inner_center = document.createElement('div');
        for (var i = 0; i < word.guesses.length; i++) {
            var span = document.createElement('span');
            span.innerText = word.guesses[i];
            if (word.guesses[i] == word.correct_guess) {
                span.className = 'summary-word-guess success-guess';
            }
            else {
                span.className = 'summary-word-guess';
            }
            inner_center.appendChild(span);
        }
        center.appendChild(inner_center);
        outer.appendChild(center);
        return outer;
    };
    GuiGame.prototype._build_word_card = function (word, hint) {
        var card = document.createElement('div');
        card.className = 'word-card';
        if ((hint === null || hint === void 0 ? void 0 : hint.length) > 0) {
            var heading = document.createElement('p');
            heading.className = "heading";
            heading.innerText = hint;
            card.appendChild(heading);
        }
        var title = document.createElement('p');
        title.className = "title";
        title.innerText = word;
        card.appendChild(title);
        return card;
    };
    GuiGame.prototype.show_word_correct = function (callback) {
        // TODO: implement show word was correct aniamtion
        callback();
    };
    GuiGame.prototype.show_word_wrong = function (callback) {
        // TODO: implement show word was wrong aniamtion
        callback();
    };
    GuiGame.prototype.show_next_word = function (word, callback) {
        this.callback_guess = callback;
        this.element_source_hint.textContent = word.source_hint1;
        this.element_source_lang.textContent = word.source;
        this.element_guess_input.value = '';
        this.element_guess_input.focus();
    };
    GuiGame.prototype.escape_html_chars = function (s) {
        return s
            .replace(/&/g, "&amp;")
            .replace(/</g, "&lt;")
            .replace(/>/g, "&gt;")
            .replace(/"/g, "&quot;")
            .replace(/'/g, "&#39;");
    };
    return GuiGame;
}());
export { GuiGame };
