var LangChooserGui = /** @class */ (function () {
    function LangChooserGui() {
        var tbody = document.getElementById('game-choose-language-tbody');
        var self = this;
        tbody.addEventListener('click', function (ev) { return self._handle_click(ev); });
    }
    LangChooserGui.prototype.hide = function () {
        document.getElementById('game-choose-language').style.display = 'none';
    };
    LangChooserGui.prototype.bind_language_picked = function (callback) {
        this._callback_language_picked = callback;
    };
    LangChooserGui.prototype.display_languages = function (languages) {
        var tbody = document.getElementById('game-choose-language-tbody');
        while (tbody.firstChild) {
            tbody.removeChild(tbody.lastChild);
        }
        for (var i = 0; i < languages.length; i++) {
            var row = this.build_language_row(languages[i], i);
            tbody.appendChild(row);
        }
        document.getElementById('game-choose-language').style.display = 'block';
    };
    LangChooserGui.prototype._handle_click = function (ev) {
        var t = ev.target;
        while (t) {
            if (t.tagName == 'TR') {
                break;
            }
            t = t.parentElement;
        }
        if (t == null)
            return;
        // Found tr!
        var index = parseInt(t.getAttribute('data-id'));
        if (isNaN(index) || index == null)
            return;
        this._callback_language_picked(index);
    };
    LangChooserGui.prototype.build_language_row = function (language, index) {
        var row = document.createElement('tr');
        row.setAttribute('data-id', index.toString());
        var td = document.createElement('td');
        td.innerText = language.source;
        row.appendChild(td);
        td = document.createElement('td');
        td.innerText = language.target;
        row.appendChild(td);
        return row;
    };
    return LangChooserGui;
}());
export { LangChooserGui };
